body {
    font-family: "Tahoma", sans-serif;
    font-size: 18px;
    line-height: 25px;
    color: #164a44;

    background-color: #b2d9d0;
}

.tasksTitle {
    margin: 50px 0 20px 0;

    text-align: center;
    text-transform: uppercase;
}

.tasksList {
    margin: 0;
    padding: 0;

    list-style: none;
}

.tasksItem {
    transition: background-color 0.5s;
    margin-bottom: 10px;
    padding: 5px;

    text-align: center;
    border: 2px dashed #b2d9d0;
    border-radius: 10px;
    cursor: move;
    background-color: #dff2ef;

    transition: background-color 0.5s;
}

.tasksItem:last-child {
    margin-bottom: 0;
}

.selected {
    opacity: 0.6;
}
